h2 {
	margin-bottom: 0;
}

.resume {
	overflow-y: scroll;
	height: calc(100vh - var(--pad) * 2);
	top: var(--pad);
	position: fixed;
	right: calc(var(--pad) * 2);
	width: 20rem;
	-ms-overflow-style: none;
	/* IE and Edge */
	scrollbar-width: none;
	/* Firefox */
}

.resume::-webkit-scrollbar {
	display: none;
}

@media screen and (max-width:815px) {
	.resume {
		top: 40vh;
		height: calc(60vh - var(--pad) * 2);
		width: calc(100vw - var(--pad) * 4)
	}
}